import React from 'react';
import { Link } from 'react-router-dom';

import classes from './SecondaryNav.module.css';

const secondaryNav = (props) => (
	<nav className={classes.SecondaryNav} data-show={props.showNav}>
		<div className={classes.SecondaryNavLinkContainer}>
			<Link className={classes.SecondaryNavLink} to="/about">About</Link>
			<Link className={classes.SecondaryNavLink} to="/contact">Contact</Link>
		</div>
	</nav>
);

export default secondaryNav;