import React from 'react';
import { Link } from 'react-router-dom';

import classes from './PrimaryNav.module.css';

const secondaryNav = (props) => {
	let button = <button onClick={props.showBtnClickedHandler}>···</button>;
	if (props.showNav) {
		button = <button onClick={props.closeBtnClickedHandler}>×</button>
	}
	
	return (
		<nav className={classes.PrimaryNav}>
			<Link className={classes.PrimaryNavLink} to="/">Refractor</Link>
			{button}
		</nav>
	);
};

export default secondaryNav;